.cont{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
    background: #ECEDF5;
    padding: 20px;
    border-radius: 7px;
    height: auto;
}
.empty{
    border-radius: 7px;
    padding: 6px;
    font-size: 15px;
    color: #5D6064;
    background: #DEDFE1;
}
ul{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0px;
    margin: 0px;
    gap: 15px;
}
